#playerList {
  background: url("../../assets/images/bucket_of_balls.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: scroll;
  height: 100vh;

  .background {
    background-color: rgba(4, 57, 39, 0.8);
    height: 100%;
  }

  .head-text {
    padding-top: 6rem;
    font-size: 5rem;
    font-weight: 800;
    text-align: center;
    color: var(--white-color);
    text-shadow: -2px 0 black;

    @media screen and (max-width: 1250px) {
      font-size: 3.75rem;
    }

    @media screen and (max-width: 450px) {
      font-size: 3rem;
    }
  }

  .secondary-text {
    padding-top: 2rem;
    font-size: 5rem;
    font-weight: 800;
    text-align: center;
    color: var(--white-color);
    text-shadow: -2px 0 black;

    @media screen and (max-width: 1250px) {
      font-size: 3.75rem;
    }

    @media screen and (max-width: 450px) {
      font-size: 3rem;
    }
  }

  .player-list {
    display: flex;
    justify-content: space-evenly;

    table {
      table-layout: fixed;
    }

    th,
    td {
      margin: 0.5rem;
      padding: 0.25rem 0.65rem;
      color: var(--yellow-color);
      text-shadow: -1px 0 black;
      font-size: 2.5rem;
      text-align: center;
    }

    td,
    a {
      margin: 0.25rem;
      color: var(--white-color);
      text-shadow: -1px 0 black;
      font-size: 2rem;
      text-decoration: none;
    }

    @media only screen and (max-width: 1650px) {
      th {
        font-size: 2rem;
      }
      td {
        font-size: 1.6rem;
      }
    }

    @media only screen and (max-width: 1250px) {
      thead th {
        display: none;
      }

      td {
        display: block;
        font-size: 3rem;
      }

      td:first-child {
        color: var(--yellow-color);
        font-size: 4rem;
      }
    }

    @media only screen and (max-width: 450px) {
      thead th {
        display: none;
      }

      td {
        display: block;
        font-size: 1.35rem;
      }

      td:first-child {
        color: var(--yellow-color);
        font-size: xx-large;
      }
    }
  }

  h4 {
    color: var(--white-color);
    text-shadow: -1px 0 black;
    font-size: 3rem;
    padding-top: 1rem;

    @media screen and (max-width: 1250px) {
      font-size: 3.5rem;
    }

    @media screen and (max-width: 450px) {
      font-size: 2.5rem;
    }
  }

  button {
    padding: 1rem 2rem;
    border-radius: 10px;
    border: none;
    background-color: var(--secondary-color);
    font-weight: 500;
    font-size: 2rem;
    color: var(--white-color);
    text-shadow: -1px 0 black;
    outline: none;
    margin: 2rem;
    font-family: var(--font-base);
    cursor: pointer;
  }

  .delete-button {
    padding: 0.3rem 0.75rem;
    border-radius: 10px;
    border: none;
    background-color: red;
    font-weight: 500;
    font-size: 1.5rem;
    color: var(--white-color);
    text-shadow: -1px 0 black;
    outline: none;
    margin: 1rem;
    font-family: var(--font-base);
    cursor: pointer;
  }

  .edit-button {
    padding: 0.3rem 0.75rem;
    border-radius: 10px;
    border: none;
    background-color: #6495ed;
    font-weight: 500;
    font-size: 1.5rem;
    color: var(--white-color);
    text-shadow: -1px 0 black;
    outline: none;
    margin: 1rem;
    font-family: var(--font-base);
    cursor: pointer;
  }

  @media screen and (max-width: 1250px) {
    background: url("../../assets/images/bucket_of_balls.jpg");
    background-size: cover;
    background-position: center;
    height: 100%;
  }
}
